import React from "react";
import { Route } from "react-router-dom";
import ArticleList from "./components/containers/ArticleListView";
import ArticleDetailView from "./components/containers/ArticleDetailView";
import Login from "./components/containers/Login.js";
import Signup from "./components/containers/Signup";
import axios from "axios";

axios.defaults.xsrfHeaderName = "X-CSRFToken";
axios.defaults.xsrfCookieName = "csrftoken";

export const BaseRouter = () => (
  <div>
    <Route exact path="/" component={ArticleList} />{" "}
    <Route exact path="/post/:articleID/" component={ArticleDetailView} />{" "}
    <Route exact path="/login/" component={Login} />{" "}
    <Route exact path="/signup/" component={Signup} />{" "}
  </div>
);

export default BaseRouter;
